import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';
import { Router, RouterModule } from '@angular/router';
import { FrontendLogger } from '@biz-nest/core-client';
import {
  AppNameService, CompanyListBase, EventbusService, MainBarTitleService,
  openClose,
  TooltipComponent,
  WindowService
} from '@front-shared-lib';
import { ToastrService } from 'ngx-toastr';
import { AdminSearchFieldComponent } from '../../misc/admin-search-field/admin-search-field.component';
import { AdminTableComponent } from '../../misc/admin-table/admin-table.component';

@Component({
  selector: 'app-select-companys',
  standalone: true,
  imports: [
    RouterModule,
    TooltipComponent,
    AdminTableComponent,
    AdminSearchFieldComponent
  ],
  animations: [
    openClose
  ],
  templateUrl: './select-companys.component.html',
  styleUrl: './select-companys.component.scss'
})
export class SelectCompanysComponent
  extends CompanyListBase
  implements OnInit, OnDestroy {
  @Input({ required: true }) selectedCompanyIds: string[] = [];
  @Output() selectedCompanys = new EventEmitter<string[]>();
  override scrollForce = false;

  constructor(
    protected override router: Router,
    protected override eventbus: EventbusService,
    protected override windowService: WindowService,
    protected override toastr: ToastrService,
    protected override apNme: AppNameService,
    protected override mainBarTitle: MainBarTitleService,
    protected override metaTagService: Meta,
    protected override titleService: Title
  ) {
    super(router, eventbus, windowService, toastr, apNme, mainBarTitle, metaTagService, titleService);
  }

  override ngOnInit(): void {
    this.listCompose.selections = this.selectedCompanyIds;
    super.ngOnInit();
  }


  selectionToggle(ev: string[]) {
    this.listCompose.toggleSelection(ev);

    FrontendLogger.debug('selections ', this.listCompose.selections);

    this.selectedCompanys.emit(this.listCompose.selections);
  }

  override ngOnDestroy() {
    super.ngOnDestroy();
  }
}

