import { DatePipe } from '@angular/common';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';
import { ActivatedRoute, Router, RouterModule } from '@angular/router';
import { AutomaticItemTax, Company } from '@biz-nest/commerce-client';
import { FrontendLogger } from '@biz-nest/core-client';
import {
  AppNameService, EventbusService, ListComposeAdminPanel, MainBarTitleService,
  openClose,
  ViewOneCompose,
  WindowService
} from '@front-shared-lib';
import { EagleInfoClientError, Icrumb, navByBus } from '@main-shared-lib';
import { ToastrService } from 'ngx-toastr';
import { AdminBackBarComponent } from '../../misc/admin-back-bar/admin-back-bar.component';
import { AdminPageContentComponent } from '../../misc/admin-page-content/admin-page-content.component';
import { AdminTableComponent } from '../../misc/admin-table/admin-table.component';
import { SelectCompanysComponent } from '../select-companys/select-companys.component';

@Component({
  selector: 'app-view-auto-tax',
  standalone: true,
  imports: [
    RouterModule,
    AdminBackBarComponent,
    DatePipe,
    AdminPageContentComponent,
    SelectCompanysComponent,
    AdminTableComponent
  ],
  animations: [
    openClose
  ],
  templateUrl: './view-auto-tax.component.html',
  styleUrl: './view-auto-tax.component.scss'
})
export class ViewAutoTaxComponent
implements OnInit, OnDestroy {
  viewOneCompose = new ViewOneCompose(
    AutomaticItemTax,
    { route: ['/auto-tax/update'], eventName: 'updateAutomaticItemTax' },
    this.router,
    this.route,
    this.eventbus
  );

  title = 'View';
  breadCrumbs: Icrumb[] = [
    {
      name: 'Dashboard',
      icon: 'space_dashboard',
      link: '/'
    },
    {
      name: 'Automatic Tax List',
      icon: 'assessment',
      link: '/auto-tax/list'
    },
    {
      name: 'View Auto Tax',
      icon: 'visibility'
    }
  ];

  actionsIncludes = [
    {
      label: 'View',
      icon: 'visibility',
      event: (item: Company) => this.viewcompany(item)
    },
    {
      type: 'delete',
      label: 'Delete',
      icon: 'delete',
      event: () => {
        if (!this.viewOneCompose.oneItem) {
          return;
        }
        this.getAffectedCompaniesInclude();
        this.getAffectedCompaniesExclude();
        this.updateAutoTax();
      }
    }
  ];

  actionsExcludes = [
    {
      label: 'View',
      icon: 'visibility',
      event: (item: Company) => this.viewcompany(item)
    },
    {
      type: 'delete',
      label: 'Delete',
      icon: 'delete',
      event: () => {
        if (!this.viewOneCompose.oneItem) {
          return;
        }
        this.getAffectedCompaniesInclude();
        this.getAffectedCompaniesExclude();
        this.updateAutoTax();
      }
    }
  ];

  selectCompaniesModalOpenIncludes = false;

  selectCompaniesModalOpenExcludes = false;

  listComposeIncludes = new ListComposeAdminPanel<Company>(
    Company,
    this.eventbus,
    this.windowService,
    this.toastr
  );

  listComposeExcludes = new ListComposeAdminPanel<Company>(
    Company,
    this.eventbus,
    this.windowService,
    this.toastr
  );

  navigateByBus = navByBus(this.eventbus, this.router).navigateByBus;


  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private eventbus: EventbusService,
    private apNme: AppNameService,
    public mainBarTitle: MainBarTitleService,
    private windowService: WindowService,
    private toastr: ToastrService,
    private metaTagService: Meta,
    private titleService: Title
  ) { }

  async ngOnInit() {
    try {
      await this.viewOneCompose.init();
      this.getAffectedCompaniesInclude();
      this.getAffectedCompaniesExclude();

      this.title = this.viewOneCompose.oneItem?.name || 'View';

      this.mainBarTitle.title = 'View Auto Tax - ' + this.title;

      this.titleService.setTitle(this.mainBarTitle.title + ' - ' + this.apNme.name);
      this.metaTagService.updateTag({
        name: 'description',
        content: `View and manage the details of
        an auto tax to ensure accurate
        information and effective business operations.`
      });
    } catch (error) {
      FrontendLogger.error(error);
    }
  }

  getAffectedCompaniesInclude() {
    if (!this.viewOneCompose.oneItem) {
      throw new EagleInfoClientError('no auto tax');
    }
    try {
      if (this.viewOneCompose.oneItem.includeOnlyIds.length === 0) {
        return;
      }

      if (this.viewOneCompose.oneItem.includeOnlyIds.length > 1) {
        this.listComposeIncludes.resetPagination();
        const propFilter = {
          _id: this.viewOneCompose.oneItem.includeOnlyIds
        };

        this.listComposeIncludes.filterBy(propFilter);
      }
    } catch (error) {
      FrontendLogger.error(error);
    }
  }


  getAffectedCompaniesExclude() {
    if (!this.viewOneCompose.oneItem) {
      throw new EagleInfoClientError('no auto tax');
    }
    try {
      if (this.viewOneCompose.oneItem.excludeIds.length === 0) {
        return;
      }

      if (this.viewOneCompose.oneItem.excludeIds.length > 1) {
        this.listComposeExcludes.resetPagination();
        const propFilterExclude = {
          _id: this.viewOneCompose.oneItem.excludeIds
        };

        this.listComposeExcludes.filterBy(propFilterExclude);
      }
    } catch (error) {
      FrontendLogger.error(error);
    }
  }


  viewCompany(company: Company) {
    this.viewOneCompose.navigateByBus(['/company/view/' + company.urId], 'oneItem', company);
  }

  toogleSelectCompaniesIncludes() {
    this.selectCompaniesModalOpenIncludes = !this.selectCompaniesModalOpenIncludes;
  }

  toogleSelectCompaniesExcludes() {
    this.selectCompaniesModalOpenExcludes = !this.selectCompaniesModalOpenExcludes;
  }

  selectedCompanysIncludes(selections: string[]) {
    if (!this.viewOneCompose.oneItem) {
      return;
    }

    this.viewOneCompose.oneItem.includeOnlyIds.push(...selections);

    FrontendLogger.debug('includeOnlyIds ', this.viewOneCompose.oneItem.includeOnlyIds);

    this.selectCompaniesModalOpenIncludes = false;

    this.updateAutoTax();
  }

  selectedCompanysExcludes(selections: string[]) {
    if (!this.viewOneCompose.oneItem) {
      return;
    }

    this.viewOneCompose.oneItem.excludeIds.push(...selections);

    this.selectCompaniesModalOpenExcludes = false;

    this.updateAutoTax();
  }

  async updateAutoTax() {
    if (!this.viewOneCompose.oneItem) {
      return;
    }

    await this.viewOneCompose.oneItem.update({
      includeOnlyIds: this.viewOneCompose.oneItem.includeOnlyIds,
      excludeIds: this.viewOneCompose.oneItem.excludeIds
    });

    this.toastr.success('Auto Tax updated successfully');

    this.getAffectedCompaniesInclude();
    this.getAffectedCompaniesExclude();
  }

  viewcompany(company: Company) {
    this.navigateByBus(
      ['/company/view/' + company.urId],
      'oneItem',
      company
    );
  }


  ngOnDestroy() {
    this.viewOneCompose.cleanup?.();
  }
}

